<template>
  <div class="page-badge">

    
    <mt-header title="热门岗位">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <nav class="tab-city">
      <mt-button @click.native="popupVisible1 = true" size="large" ref="button">浙江省</mt-button>
      <mt-button @click.native="popupVisible2 = true" size="large" ref="button">杭州市</mt-button>
      <mt-popup v-model="popupVisible1" popup-transition="popup-fade" class="mint-popup" :style="{ top: buttonBottom + 'px' }">
        <ul>
          <li>安徽省</li>
          <li>江苏省</li>
          <li class="active">浙江省</li>
          <li>北京市</li>
          <li>江西省</li>
          <li>湖北省</li>
          <li>湖南省</li>
        </ul>
      </mt-popup>
      <mt-popup v-model="popupVisible2" popup-transition="popup-fade" class="mint-popup" :style="{ top: buttonBottom + 'px' }">
        <ul>
          <li>杭州市</li>
          <li>温州市</li>
          <li class="active">台州市</li>
          <li>丽水市</li>
          <li>湖州市</li>
          <li>嘉兴市</li>
        </ul>
      </mt-popup>
    </nav>

    <div class="page-loadmore-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
      <mt-loadmore :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore">
        <ul class="page-loadmore-list">
          <li v-for="(value, key) in list" class="page-loadmore-item">
            <mix-box 
              image="http://www.cssoul.com/demo/weihai/company/1.jpg" 
              title="大堂经理" 
              salary="【3500-5000】" 
              source="麦当劳 McDonald's">
            </mix-box>
            <ul class="item-foot">
              <li>
                <i class="common-icon icon-location"></i>
                <span>杭州</span>
              </li>
              <li>
                <i class="common-icon icon-gift"></i>
                <span>包吃住</span>
                <span>缴纳社保</span>
                <span>福利优厚</span>
              </li>
            </ul>
          </li>
        </ul>
        <div slot="bottom" class="mint-loadmore-bottom">
          <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">↑</span>
          <span v-show="bottomStatus === 'loading'">
            <mt-spinner type="snake"></mt-spinner>
          </span>
        </div>
      </mt-loadmore>
    </div>
    
</div>
</template>

<script>
import commonTitle from '../common/title.vue'
import mixBox from '../common/mixBox.vue'

export default {
  name: 'page-list',

  components: {
      commonTitle,
      mixBox
  },

  data() {
      return {
        popupVisible1: false,
        popupVisible2: false,
        buttonBottom: 0,
        list: [],
        allLoaded: false,
        bottomStatus: '',
        wrapperHeight: 0
      };
    },

    methods: {
      handleBottomChange(status) {
        this.bottomStatus = status;
      },

      loadBottom() {
        setTimeout(() => {
          let lastValue = this.list[this.list.length - 1];
          if (lastValue < 10) {
            for (let i = 1; i <= 5; i++) {
              this.list.push(lastValue + i);
            }
          } else {
            this.allLoaded = true;
          }
          this.$refs.loadmore.onBottomLoaded();
        }, 1500);
      }
    },

    created() {
      for (let i = 1; i <= 20; i++) {
        this.list.push(i);
      }
    },

    mounted() {
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;

      this.buttonBottom = this.$refs.button.$el.getBoundingClientRect().bottom+1;
    }
};
</script>

<style>
  .page-list{
    background:#f0f0f0;
  }

  .tab-city{
    display:flex;
  }
  .tab-city .mint-button {
    flex:1;
    background:#fff;
    border-radius:0;
  }
  .tab-city .v-modal{
    top: 86px;
  }

  .mint-popup{
    width: 100%;
    height: 250px;
    padding: 0;
    transform: translate(-50%, 0);
    overflow:scroll;
  }
  .mint-popup ul{
    list-style:none;
    margin:10px 0;
    padding:0 20px;
  } 
  .mint-popup ul li{
    height:40px;
    line-height:40px;
    color:#828282;
  }
  .mint-popup ul .active{
    color: #0977C5;
  }


  .page-loadmore-list{
    list-style:none;
    margin:0;
    padding:0;
  }
  .page-loadmore-item{
    list-style:none;
    margin:10px 0;
    padding:20px 15px;
    background: #FFFFFF;
    border-top: 1px solid #E9E9E9;
    border-bottom: 1px solid #E9E9E9;
  }

  .page-loadmore-item .item-foot{
    margin:20px 0 0 0;
    padding: 15px 0 0 0;
    border-top: 1px dashed #E9E9E9;
    list-style:none;
    display:flex;
  }

  .page-loadmore-item .item-foot li{
    margin-right:20px;
    font-size:15px;
    color:#828282;
  }
  .page-loadmore-item .item-foot li span{
    margin:0 2px;
    vertical-align: middle;
  }
  .page-loadmore-item .icon-location{
    width: 15px;
    height: 15px;
    margin:0;
    background:url(../../images/location_grey_icon@3x.png) 0% 100% no-repeat;
    background-size: 15px auto;
  }

  .page-loadmore-item .icon-gift{
    width: 15px;
    height: 15px;
    margin:0;
    background:url(../../images/welfare_grey_icon@3x.png) 0% 100% no-repeat;
    background-size: 15px auto;
  }

  .mint-loadmore-bottom span {
      display: inline-block;
      transition: .2s linear;
      vertical-align: middle;
  }
  .mint-loadmore-bottom span.rotate {
      transform: rotate(180deg);
  }
  
</style>



