<template>
  <div>
    <router-view></router-view>
  </div>
</template>

<style>
  @reset-global mobile;

  html, body {
    margin:0;
    background-color: #fafafa;
    -webkit-overflow-scrolling: touch;
    user-select: none;
  }

  a {
    color: inherit;
  }


</style>

<script type="text/babel">


  export default {
    computed: {
      visible() {
        return ['/', '/header', '/search'].indexOf(this.$route.path) < 0;
      }
    }
  };
</script>



