<template>

  <div class="mix-box">
    <img class="mix-pic" :src="image"  />
    <div class="mix-info">
      <h3 class="mix-title">{{ title }} <span class="orange">{{ salary }}</span></h3>
      <p class="mix-desc">{{ source }}</p>
    </div> 
  </div>

</template>

<script>

  export default {
    name: 'mix-box',

    props: {
      image: {
        type: String,
        default: ''
      },
      title: {
        type: String,
        default: ''
      },
      salary: {
        type: String,
        default: ''
      },
      source: {
        type: String,
        default: ''
      }
    }

  };
</script>

<style lang="css">

  .mix-box{
    display: flex;
  }
  .mix-box .mix-pic{
    width:60px;
    height:60px;
    border-radius: 30px;
  }
  .mix-box .mix-info{
    flex: 1;
    margin-left:10px;
    color: #1E1E1E;
  }
  .mix-box .mix-title{
    margin:10px 0;
    font-size: 17px;
    line-height: 20px;
  }
  .mix-box .mix-desc{
    margin:0;
    font-size: 15px;
    line-height: 16px;
  }
  
</style>



